<template>
  <div class="seePage">
    <!--  查看销售退货单 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>售后管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">三包退货</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看三包退货</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="销售组织：">
          {{ TableData.company_name }}
        </el-form-item>
        <el-form-item label="单据号：">
          {{ TableData.return_sn }}
        </el-form-item>
        <el-form-item label="业务日期：">
          {{ TableData.business_time }}
        </el-form-item>
        <el-form-item label="客户名称：">
          {{ TableData.customer_name }}
        </el-form-item>
        <el-form-item label="部门：">
          {{ TableData.department_name }}
        </el-form-item>
        <el-form-item label="业务员：">
          {{ TableData.business_name }}
        </el-form-item>
        <el-form-item label="来源单号：">
          {{ TableData.outstock_sn }}
        </el-form-item>
        <el-form-item label="意向物流：">
          {{ TableData.intentional_logistics }}
        </el-form-item>
        <el-form-item label="退货原因：">
          {{ TableData.reason_name }}
        </el-form-item>
        <el-form-item label="单据状态：">
          {{ TableData.auditor_status_name }}
        </el-form-item>
        <el-form-item label="备注：">
          {{ TableData.remark }}
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_info" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" width="50" label="序号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="80">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" width="400">
          <template slot-scope="scope">
            <pre>{{ scope.row.goods_name }}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" width="88">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="数量" property="return_goods_num" type="number" width="70" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_price" label="销售单价" width="100" align="right">
          <template slot-scope="scope">
            <pre>{{ scope.row.true_unit_price | priceFormatNone }}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_subtotal_price" label="金额" align="right" width="100">
          <template slot-scope="scope">
            <pre>{{ scope.row.return_subtotal_price | priceFormatNone }}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库" width="132">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位" width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" :render-header="renderHeaderpro" property="production_time" width="80" label="生产日期">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位" width="88">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>

      <div class="leftfootlist" v-if="TableData.outstock_type_id==10002 && TableData.outstock_sn">
        <span class="yeban">本单退还返利：{{ TableData.rebate_price }}</span>
        <!-- <span class="yeban">本单退还授信：{{ TableData.credit_price }}</span> -->
        <span class="yeban">本单退还预收：{{ TableData.deposits_price }}</span>
      </div>

      <div class="refundauditFoot">
        <span>制单人：{{ TableData.add_user_name }}</span>
        <span>单据时间：{{ TableData.add_time }}</span>
        <span>审核人：{{ TableData.auditor_name }}</span>
        <span>审核时间：{{ TableData.auditor_time }}</span>
        <span>修改人：{{ TableData.update_user_name }}</span>
        <span>修改时间：{{ TableData.update_time }}</span>
      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <template v-if="TableData.after_auditor_print_status == 1">
        <el-button @click="printSheet" type="primary" v-if="authority.b_print == 1 && TableData.auditor_status == 2">打印</el-button>
      </template>
      <template v-else>
        <el-button @click="printSheet" type="primary" v-if="authority.b_print == 1">打印</el-button>
      </template>

      <template v-if="TableData.auditor_status == 2">
        <!-- 已审核 -->
        <el-button @click="audit('auditInvalidThressReturn')" v-if="authority.b_invalid == 1">作废</el-button>
        <el-button @click="auditNoThressReturn" v-if="authority.b_nopass == 1">弃审</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="shenhe" v-if="authority.b_pass == 1">审核</el-button>
        <el-button @click="audit('auditInvalidThressReturn')" v-if="authority.b_invalid == 1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="
          TableData.auditor_status == 3 ||
            (authority.b_invalid != 1 && authority.b_pass != 1)
        ">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>

    <div v-if="dialogUserVisible">
      <el-dialog :close-on-click-modal="false" title="审核确认" class="dialogForm" :visible.sync="dialogUserVisible" width="800px">
        <div style="width: 750px;">
          <span style="color:red">提示：由于以下商品无结存成本或者结存成本为0，请先输入成本单价！</span>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="leftList" height="242" style="width: 750px;float:left;margin-top:5px;cursor:pointer">
            <el-table-column :show-overflow-tooltip="true" property="company_name" width="100" label="销售组织"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="100" label="货位"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="goods_id" width="100" label="商品编号"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="goods_name" width="138" label="商品名称"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="batch" width="100" label="年周/批次"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="cost_price" width="110" label="成本单价" :render-header="renderHeader">
              <template slot-scope="scope">
                <el-input size="small" type="number" @change="getcost(scope.row, scope.$index)" v-model="scope.row.cost_price" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogUserVisible = false" style="margin-top:10px">取 消</el-button>
          <el-button class="tx" type="primary" @click="submitUserForm()" style="margin-top:10px">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <publicPrint ref="printDelieveryBill" @Search="onSearch" title="threeReturn"></publicPrint>
  </div>
</template>

<script>
import publicPrint from "common/components/public/PublicPrint.vue"; //公共打印
export default {
  name: "salesSalesReturnSee",
  data() {
    return {
      authority: {},
      TableData: {},
      printFlag: false,
      dialogUserVisible: false,
      chaid: false,
      hasAuditorStatusPrint: null
    };
  },
  methods: {
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    checkThreeReturn() {
      this.$axios({
        method: "post",
        url: this.$api.auditThressReturn,
        data: {
          return_id: this.TableData.return_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg || res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 弃审
    auditNoThressReturn() {
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: this.TableData.return_id,
          operation_type: 5
        })
        .then(() => {
          this.$axios({
            url: this.$api.auditNoThressReturn,
            method: "post",
            data: {
              return_id: this.TableData.return_id
            }
          }).then(({ data }) => {
            if (data.code == 200) {
              this.$message({
                message: data.msg || data.data,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    },
    shenhe() {
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: this.TableData.return_id,
          operation_type: 4
        })
        .then(() => {
          this.$axMethod
            .checkThreeReturnGoods({
              return_id: this.TableData.return_id
            })
            .then(({ data }) => {
              if (data.code == 200) {
                if (data.data.is_success == 1) {
                  this.checkThreeReturn();
                } else {
                  this.leftList = data.data;
                  this.dialogUserVisible = true;
                }
              }
            });
        });
    },
    printSheet() {
      this.printFlag = true;
      this.$nextTick(() => {
        this.$refs.printDelieveryBill.printPdf({
          return_id: this.TableData.return_id
        });
      });
    },
    renderHeader(h, { column, $index }) {
      return (
        <span class="start" style="cursor:pointer">
          {" "}
          {column.label}{" "}
        </span>
      );
    },
    submitUserForm() {
      for (var i = 0; i < this.leftList.length; i++) {
        if (
          this.leftList[i].cost_price == undefined ||
          this.leftList[i].cost_price == null ||
          this.leftList[i].cost_price == ""
        ) {
          this.$message({
            message: "成本单价不能为空且必须为正数",
            type: "warning"
          });
          return false;
        }
      }
      this.checkThreeReturn();
    },
    getcost(row, index) {
      if (row.cost_price < 0 || isNaN(row.cost_price)) {
        this.$message.warning("成本单价不能为空且必须为正数");
        this.$set(this.leftList[index], "cost_price", "");
      } else if (row.cost_price >= 1e8) {
        this.$message.warning("数值不能超过一亿");
        this.$set(this.leftList[index], "cost_price", 0);
      } else {
        let a = parseFloat(Number(row.cost_price)).toFixed(2);
        this.$set(this.leftList[index], "cost_price", a);
      }
    },
    goBack() {
      this.$router.push({ path: "/aftersale/threepackagereturn" });
    },
    audit(api) {
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: this.TableData.return_id,
          operation_type: 6
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api[api],
            data: {
              return_id: this.TableData.return_id
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg || res.data.data,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "return_goods_num") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
        }

        if (column.property === "return_subtotal_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number((prev + curr).toFixed(2));
          }, 0);
          sums[index] = sums[index]
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        }
      });
      return sums;
    }
  },
  components: {
    publicPrint
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.$axios({
      method: "get",
      url: this.$api.SeeThressReturn,
      params: {
        return_id: this.$route.query.id,
        return_sn: this.$route.query.return_sn
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        data.data.goods_info.forEach(obj => {
          obj.unit_price = obj.return_unit_price;
        });
        this.$set(this, "TableData", data.data);
      }
    });
  }
};
</script>

<style scoped>
.yeban {
  color: #606266;
  font-size: 12px;
}
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}

.el-form-item {
  width: 320px;
}

.tips li {
  float: right;
  line-height: 50px;
  margin-left: 15px;
}
</style>
